import * as React from "react";
import Icon from "../Icon";
import data from "../../api/data";
import "./Socials.scss";

function _Socials(props) {
  const {socials, style, fontSize, detail} = props;
  const classes = ['socials'];
  if (detail) {
    classes.push('detail');
  }

  function Item({ social }) {
    return (
      <li>
        <a href={social.url || null}>
          <Icon icon={social.icon} style={{fontSize: fontSize}} />
          {detail && social.content
          }
        </a>
      </li>
    );
  }

  return (
    <ul className={classes.join(' ')} style={style}>
      {socials.map((social, i) => (
        <Item key={i} social={social} />
      ))}
    </ul>
  );
}


export default function Socials(props) {
  const socials = data.socials || [];
  return <_Socials socials={socials} {...props}/>;
}